function emitUpdate() {
  if (window.confirm("È°µÈù¢ÊúâÊõ¥Êñ∞ ËØ∑Á°ÆËÆ§!") === true) {
    try {
      navigator.serviceWorker.getRegistration().then((reg: any) => {
        console.log("ÈÄöÁü• sw Êõ¥Êñ∞");
        reg.waiting.postMessage("skipWaiting");
      });
    } catch (e) {
      window.location.reload();
    }
  }
}

const registerServiceWorker = async () => {
  if ("serviceWorker" in navigator) {
    try {
      const registration = await navigator.serviceWorker.register(
        "/service-worker.js",
        {
          scope: "/",
        }
      );

      if (registration.waiting) {
        emitUpdate();
        return;
      }

      if (registration.installing) {
        console.log("Ê≠£Âú®ÂÆâË£Ö Service worker");
      } else if (registration.waiting) {
        console.log("Â∑≤ÂÆâË£Ö Service worker installed");
      } else if (registration.active) {
        console.log("ÊøÄÊ¥ª Service worker");
      }

      // ÈÅøÂÖçÊó†ÈôêÂà∑Êñ∞
      registration.onupdatefound = function () {
        const installingWorker = registration.installing;
        console.log(
          "üöÄ ~ file: initServiceWorker.ts:33 ~ registerServiceWorker ~ installingWorker:",
          installingWorker
        );

        if (installingWorker) {
          installingWorker.onstatechange = function () {
            switch (installingWorker.state) {
              case "installed":
                if (navigator.serviceWorker.controller) {
                  // Êõ¥Êñ∞
                  emitUpdate();
                }
                break;
            }
          };
        }
      };
    } catch (error) {
      console.error(`Ê≥®ÂÜåÂ§±Ë¥•Ôºö${error}`);
    }
  }
};

registerServiceWorker();

export {};
